import React from "react";
import Intro from "../components/Intro";
import Card from "../components/Card";

const intro = {
  content: `Semangat kami adalah menjadi bagian dari solusi. Dengan kurikulum yang berorientasi pada industri dan metode belajar yang telah teruji, kami ingin mendorong lahirnya banyak programmer baru yang siap menjadi solusi untuk berbagai permasalahan yang ada di Indonesia.`
};

const cards = [
  {
    title: `Weekend Bootcamp`,
    imageUrl: `https://devschool.id/resources/themes/default/assets/img/office-13.jpg`,
    subtitle: `Kelas belajar coding yang diselenggarakan di setiap akhir pekan, berlokasi di Bandung dan Jakarta`
  },
  {
    title: `Intensive Bootcamp`,
    imageUrl: `https://devschool.id/resources/themes/default/assets/img/office-10.jpg`,
    subtitle: `Kelas belajar pemrograman Web dan Android secara intensif selama satu minggu dengan sistem karantina
      `
  }
];

const DevSchool = () => (
  <div className="container">
    <h1 style={{ textAlign: "center" }}>Developer School</h1>
    <div className="row" style={{ marginTop: 40, marginBottom: 100 }}>
      <Intro content={intro.content} />
      <div className="col-md-6">
        <img
          src="https://devschool.id/resources/themes/default/assets/img/gallery_5.jpg"
          alt=""
        />
      </div>
    </div>
    <div className="row" style={{ marginTop: 50 }}>
      {cards.map(card => (
        <Card
          imageUrl={card.imageUrl}
          title={card.title}
          subtitle={card.subtitle}
        />
      ))}
    </div>
  </div>
);

export default DevSchool;
