import React from "react";

const Home = () => {
  return (
    <div style={styles.container}>
      <img
        style={styles.imageCover}
        src="https://devschool.id/resources/themes/default/assets/img/bg-school.jpg"
        alt="Belajar Coding"
      />
      <div style={styles.blockText}>
        <h1>Belajar Coding Sekarang</h1>
        <h4>
          Semangat kami adalah menjadi bagian dari solusi. Dengan kurikulum yang
          berorientasi pada industri dan metode belajar yang telah teruji, kami
          ingin mendorong lahirnya banyak programmer baru yang siap menjadi
          solusi untuk berbagai permasalahan yang ada di Indonesia.
        </h4>
      </div>
    </div>
  );
};

const styles = {
  container: {
    position: "relative",
    textAlign: "center"
  },
  imageCover: {
    width: "100%"
  },
  blockText: {
    width: "100%",
    position: "absolute",
    top: "50%",
    left: "50%",
    transform: "translate(-50%, -50%)",
    color: "white"
  }
};

export default Home;
