import React from "react";
import Intro from "../components/Intro";
import Card from "../components/Card";

const intro = {
  content: `Program membership merupakan program belajar pemrograman di CodePolitan dengan sistem keanggotaan. Member yang bergabung dalam program membership ini bisa mengakses semua konten di CodePolitan baik yang gratis ataupun yang berbayar selama masa keanggotaan masih aktif. Masa keanggotaan dalam program membership ini memiliki batas waktu sesuai dengan paket belajar yang dipilih oleh member saat bergabung dalam program. Member dapat mempepanjang dan menghentikan keanggotaan dalam program membership ini kapan pun yang dia inginkan. Dalam program ini, member bisa belajar apa saja sesuai dengan yang ia inginkan tanpa harus membeli kelas online satu per satu.`
};

const features = [
  {
    title: `Alur Belajar Jelas`,
    desc: `Kamu tidak perlu lagi kebingungan harus mulai dari mana. Kami sudah menyusun alur yang sangat jelas untuk membantumu belajar coding lebih terarah.`
  },
  {
    title: `Waktu Belajar Fleksibel`,
    desc: `Kamu bisa belajar kapan saja dan di mana saja. Kamu bahkan bisa belajar dari rumahmu sendiri di sela-sela kesibukanmu.`
  },
  {
    title: `Project Base`,
    desc: `Kamu tidak hanya akan belajar teori yang membingungkan. Kamu juga akan belajar bagaimana menerapkannya dalam contoh project. 
    `
  },
  {
    title: `Sertifikat Digital`,
    desc: `Kamu bisa mendapatkan sertifikat digital dengan cara menyelesaiakan alur belajar yang telah ditentukan.`
  }
];

const cards = [
  {
    title: `Membuat Sistem Informasi Perpustakaan Menggunakan Laravel 6`,
    imageUrl: `https://static.cdn-cdpl.com/source/2/Membuat_Sistem_Informasi_Perpustakaan_Menggunakan_Laravel_6.jpg`
  },
  {
    title: `Membuat Web Kuliner Menggunakan React.js`,
    imageUrl: `https://static.cdn-cdpl.com/source/2/Membuat_Web_Kuliner_Menggunakan_React.jpg`
  },
  {
    title: `Membuat Aplikasi Mobile Pemesanan Tiket Online`,
    imageUrl: `https://static.cdn-cdpl.com/source/2/Membuat_Aplikasi_Mobile_Pemesanan_Tiket_Online.jpg`
  },
  {
    title: `Membangun Website Toko Online Berbasis CodeIgniter`,
    imageUrl: `https://static.cdn-cdpl.com/source/2/Membangun_Website_Toko_Online_Berbasis_CodeIgniter.jpg`
  }
];

const Premium = () => (
  <div className="container">
    <h1 style={{ textAlign: "center" }}> Premium Online Course </h1>
    <div className="row" style={{ marginTop: 40, marginBottom: 100 }}>
      <Intro content={intro.content}></Intro>
      <div className="col-md-6" style={styles.box450border}>
        {features.map(feature => (
          <div>
            <h5>
              <strong>{feature.title}</strong>
            </h5>
            <p>{feature.desc}</p>
          </div>
        ))}
      </div>
    </div>
    <div className="row" style={{ marginBottom: 50 }}>
      {cards.map(card => (
        <Card imageUrl={card.imageUrl} title={card.title} />
      ))}
    </div>
  </div>
);

const styles = {
  box450border: {
    height: "450px",
    border: "2px solid #f1f1f1",
    borderRadius: 10,
    padding: 30
  }
};

export default Premium;
